.close-container {
  width: 50px;
  height: 50px;
  cursor: pointer;
  margin-top: 20px;
  margin-left: 20px;
  position: fixed;
}

.leftright {
  height: 4px;
  width: 50px;
  background-color: #bd00ff;
  border-radius: 2px;
  margin-top: 24px;
  transition: all .3s ease-in;
  position: absolute;
  transform: rotate(45deg);
}

.rightleft {
  height: 4px;
  width: 50px;
  background-color: #bd00ff;
  border-radius: 2px;
  margin-top: 24px;
  transition: all .3s ease-in;
  position: absolute;
  transform: rotate(-45deg);
}

.close-container:hover .leftright {
  background-color: #f25c66;
  transform: rotate(-45deg);
}

.close-container:hover .rightleft {
  background-color: #f25c66;
  transform: rotate(45deg);
}

